<template lang='pug'>
	.wrapper
		.title
			h1 {{title}}
		.tips {{tips}}
		.form
			uni-forms(
				ref="form"
				class='my-form'
				:modelValue="formData"
				:rules="rules"
				label-position="top"
			)
				.title
					span(class='require') *
					span 申报人信息
				.form-content
					uni-forms-item(label="姓名:" name="name")
						uni-easyinput(type="text" v-model="formData.name" placeholder="请输入姓名")
					uni-forms-item(label="职务:" name="duty")
						uni-easyinput(type="text" v-model="formData.duty" placeholder="请输入职务")
					uni-forms-item(label="电话:" name="phone")
						uni-easyinput(type="number" v-model="formData.phone" placeholder="请输入电话")
					uni-forms-item(label="微信:" name="weixin")
						uni-easyinput(type="text" v-model="formData.weixin" placeholder="请输入微信")
	
				.title
					span(class='require') *
					span 抖音店铺信息
				.form-content
					uni-forms-item(label="店铺名称及店铺长ID:" name="shopInfo")
						uni-easyinput(type="text" v-model="formData.shopInfo" placeholder="请输入店铺名称及店铺长ID")
					uni-forms-item(label="店铺DSR评分:" name="duty")
						uni-easyinput(type="text" v-model="formData.dsr" placeholder="请输入店铺DSR评分")
						
						.submit
							button(@click="submitForm") 提交
</template>	
				
<script>
	import {test} from '../../service/apis/index.js'
	export default {
		data() {
			return {
				title: '抖音直播间商品申报表',
				tips:'尊敬的商家伙伴，您好！请您按照如下要求填写相关信息进行直播带货需求提报，为避免无效沟通，请致以最优惠的价格及最诚挚的合作意愿，感谢支持！',
				formData:{},
				rules: {
					name: {
						rules: [{
								required: true,
								format:'string',
								errorMessage: '请输入姓名',
							},
							{
								minLength: 1,
								maxLength: 8,
								errorMessage: '姓名长度在 {minLength} 到 {maxLength} 个字符',
							}
						],
						validateTrigger:'submit'
					},
					duty: {
						rules: [{
							required: true,
							format: 'string',
							errorMessage: '请输入职务',
						}],
						validateTrigger:'submit'
					},
					phone: {
						rules: [{
							required: true,
							format: 'number',
							errorMessage: '请输入电话',
						}],
						validateTrigger:'submit'
					},
					weixin: {
						rules: [{
							required: true,
							format: 'string',
							errorMessage: '请输入微信',
						}],
						validateTrigger:'submit'
					}
				}
			}
		},
		mounted(){
				test()
		},
		methods: {
			submitForm(){
				this.$refs.form.validate().then(res=>{
					console.log('表单数据信息：', res);
				}).catch(err =>{
					console.log('表单错误信息：', err);
				})
			},
			onShareAppMessage(res) {
			    if (res.from === 'button') {// 来自页面内分享按钮
					console.log(res.target)
			    }
			    return {
					title: '自定义分享标题',
					path: '/pages/form/index'
			    }
			  },
		}
	}
</script>

<style lang="scss">
	.wrapper{
		width: 100%;
		height: 100%;
		// background-color: #FFF;
		.title {
			padding: 26rpx 10rpx 12rpx;
			h1 {
				font-size: 40rpx;
				line-height: 32rpx;
				font-weight: bold;
				margin: 0;
				padding: 0;
				text-align: center;
				color: #0095ff;
			}
		}
		.tips{
			padding: 10rpx 0 26rpx;
			margin: 0 10rpx 16rpx;
			border-bottom: 1rpx dashed #ccc;
			color: #e95d00;
			text-align: center;
			font-size: 20rpx;
			font-height:24rpx;
		}
		.form{
			padding: 10rpx 14rpx;
			margin: 10rpx 6rpx;
			.my-form{
				.title{
					.require{
						color: #FF4040;
					}
				}
				.form-content{
					margin: 0rpx 26rpx;
					.submit{
						button{
							    font-size: 16px;
							    height: 44px;
							    line-height: 44px;
							    font-weight: 500;
							    border-radius: 2px;
							    color: #fff;
							    background: #0095ff;
						}
					}
					::v-deep .uni-forms-item__label{
						width: fit-content!important;
					}
				}
				
			}
		}
	}
	
</style>
